.wyswig a{
  color: var(--main-color);
}

.wyswig a:hover{
    color: var(--main-dark-color);
    text-decoration: none;
}

.wyswig h2{
  font-size: 20px;
  font-weight: bold;
  margin-top: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--alt-dark-color)
}



