#buy-ticket-form{
  padding: 40px;
  background: var(--main-color);
  margin: 40px;
}

#buy-ticket-form h1{
  text-align: center;
  font-size: 24px;
  margin-bottom: 40px;
  color: var(--alt-color)
}

#buy-ticket-form h1 .fa{
  margin-right: 5px;
}

#buy-ticket-form .buy-ticket-summary{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
  border-top: 3px solid var(--main-inv-color);
  padding: 10px 0 10px;
  color: var(--main-inv-color)
}

#buy-ticket-form .buy-ticket-summary span{
  -ms-flex: 1 1;
      flex: 1 1;
  text-transform: uppercase;
}

#buy-ticket-form .buy-ticket-summary span.amount{
  text-align: right;
  font-weight: bold;
  font-size: 48px;
}

@media screen and (max-width: 480px) {
  #buy-ticket-form{
      padding: 20px
    }
}



